@font-face {
	font-family: "Webnar - Black";
	src: url(fonts/webnar/Webnar_Black.otf);
	font-weight: 400;
	font-display: swap;
}

@font-face {
	font-family: Arial, sans-serif;
	src: url("/templates/main/build/fonts/arial/ArialMT.eot");
	src: local("__"), url("/templates/main/build/fonts/arial/ArialMT.woff") format("woff"), url("/templates/main/build/fonts/arial/ArialMT.svg") format("svg");
	font-weight: normal;
	font-style: normal;
	font-display: swap;
}

@font-face {
	font-family: "HelveticaNeueCyr-Roman";
	src: url("../fonts/helvetica/HelveticaNeueCyr-Roman.eot");
	src: url("../fonts/helvetica/HelveticaNeueCyr-Roman.woff2") format("woff2"),
		url("../fonts/helvetica/HelveticaNeueCyr-Roman.eot?#iefix") format("embedded-opentype"),
		url("../fonts/helvetica/HelveticaNeueCyr-Roman.woff") format("woff"),
		url("../fonts/helvetica/HelveticaNeueCyr-Roman.ttf") format("truetype");
	font-weight: normal;
	font-style: normal;
	font-display: fallback;
}

